
$rating-stars: (1, 2, 3, 4, 5);

%font-size12-weight500 {
  font-size: 12px;
  font-weight: 500;
}

$blueColor: #00acdc;

@mixin body-style {
  font-family: "Roboto", sans-serif;
  background-color: #fff;
  margin: 0;
}

@mixin font-card {
  font-size: 11px;
  line-height: 13px;
}

@mixin flex-align-center {
  display: flex;
  align-items: center;
}
